$saturatedOrange: #f44a24;
$silver: #bababa;
$red: #ff005c;
$offWhite: #eaeaea;
$negativeSpace: #ecf2ff;

:root {
    --logo-background-color: #066855;
    --location-color: #232551;
    --logo-text-color: #bababa;
    --color-1: #008d70;
    --color-2: #008d70;
    --color-3: #008d70;
    --button-color: #03715c;
    --button-secondary-color: #0c4b3f;
    --analogous-color: #0088c3;
    --alternate-color: #08a184;
    --negative-space: #0e063b;
    --background-color: #110531;
}

body {
    flex: 1;
    display: flex;
    flex-direction: column;
    background-color: var(--background-color);
}

.universal-sequences-logo {
    padding: 10px;
    font-size: 24px;
    cursor: pointer;
    background-color: var(--logo-background-color);
    color: var(--logo-text-color);
}

#root {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.navigator {
    background-color: var(--negative-space);
}

.user-page {
    flex: 1;
    min-height: 1000px;
    background-color: var(--background-color);
    color: var(--color-3);
    height: 100%;
    display: flex;
    flex-direction: column;
    .btn-secondary, .btn-secondary:hover, .btn-secondary:active, .btn-secondary:visited, .btn-secondary:focus {
        border-color: #333333 !important;
        color: white !important;
    }

    .common-button {
        color: white;
    }
}

.tag-sound-input-box {
    height: 40px !important;
    width: 300px !important;
    font-size: 24px !important;
    background-color: white !important;
    color: black !important;
}

.profile-container {
    flex: 1;
    display: flex;
    flex-direction: column;
    background-color: var(--negative-space);
    width: 90%;
    margin: auto;
    min-height: 100%;
    height: 100%;
}

.common-button {
    border-style: none;
}

.banner {
    background-color: var(--color-2);
    height: 180px;
}

.user-info {
    padding-top: 25px;
}

.user-name {
    font-size: 24px;
    line-height: 37px;
    background-color: white;
    padding: 4px 7px;
}

.user-location {
    font-size: 14px;
    color: white;
    line-height: 28px;
    background-color: var(--location-color);
    padding: 4px 7px;
}

.user-main-info-container {
    float: left;
    margin-left: 25px;
}

.user-profile-picture-container {
    float: left;
    margin-left: 25px;
}

.user-no-profile-picture {
    height: 100px;
    width: 100px;
    background-color: var(--location-color);
}

.profile-details {
    flex: 1;
    display: flex;
}

.ethereum-action {
    padding-left: 25px;
    padding-top: 10px;
}

.add-spotlight-track {
    cursor: pointer;
    margin: 10px;
    font-size: 28px;
    color: #ff015a;
}

.add-spotlight-track:hover {
    color: var(--color-1);
}

.spotlight-pane {
    float: left;
}

.sequences-chooser-wrapper {
    margin-bottom: 10px;
    padding-top: 5px;
    padding-bottom: 20px;
    background-color: var(--color-1);
    color: white;
    width: 500px;

    #search-input {
        background-color: $offWhite !important;
        color: black !important;
    }

    .palette-container {
        background-color: transparent;
    }

    .field-pagination-button {
        color: var(--color-1) !important;
        background-color: #eaeaea;
    }

    .field-pagination-button:hover {
        color:  #fd5729f !important;
    }

    .sample-pack-item {
        background-color: transparent;
        .sample-pack-user-name {
            background-color: #eaeaea;
            color: var(--color-1);
        }

        .sample-pack-name {
            background-color: #eaeaea;
            color: var(--color-2);
        }
    }

    .sample-pack-item.odd {
        background-color: transparent;
    }
}

.left-pane {
    width: 50%;
}

.spotlight-track {
    padding: 15px;
    margin: 10px;
    border-width: 2px;
    border-style: solid;
    border-color: var(--color-2);
    background-color: var(--color-2);

    .playpause label {
        border-color: transparent transparent transparent black;
    }

    .sub-sequence-buttons {
        display: flex;

        .sub-sequence {
            background-color: white;
            margin-right: 3px;
            color: black;
            font-size: 8pt;
            padding: 4px;
            cursor: pointer;
        }

        .sub-sequence.selected {
            background-color: red;
            color: white;
        }
    }

    .date-and-name {
        display: flex;
        margin-bottom: 5px;
        font-size: 8pt;
    }

    .track-user-name {
        padding: 5px;
        background-color: $offWhite;
        margin-right: 5px;
    }
}

.delete-button {
    background-color: white;
    border-width: 2px;
}

.track-name-unloaded:hover {
    background-color: #c7f6eb;
}

.track-name {
    color: var(--color-1);
    background-color: #eaeaea;
    padding: 5px;
    margin-right: 10px;
    font-size: 18px;
    cursor: pointer;
    float: left;
}

.pattern-buttons {
    height: 30px;
}

.pattern-button {
    float: left;
    margin-right: 5px;
    padding-left: 5px;
    padding-right: 5px;
    background-color: white;
    color: black;
    cursor: pointer;
    font-size: 8pt;
}

.pattern-button.current {
    background-color: #4672d6;
    color: white;
}

.playpause-user {
    float: left;
    margin-top: 2px;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 10px;
}

.playpause-user label {
    height: 18px;
}

.playpause-user input[type="checkbox"]:checked + label {
  border-style: solid;
  height: 18px !important;
  border-width: 10px 0 10px 12px !important;
}

.edit-button {
    font-size: 12px;
    float: right;
    margin-top: 70px;
}

.volume-pane {
    float: left;
    margin-right: 10px;
}

.track-pane {
    float: left;
}

.volume-pane > .slider {
    height: 100px;
}


.profile-navigator {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    padding-bottom: 10px;
    border-bottom-style: solid;
    border-bottom-width: thin;
}

.profile-navigator > .navigator-option {
    padding: 5px;
    margin-right: 20px;
    font-size: 12pt;
    cursor: pointer;
    padding-bottom: 5px;
    border-style: solid;
    border-width: 1px;
    border-left-style: none;
    border-top-style: none;
}

.profile-navigator > .navigator-option.selected {
    border-bottom-style: solid;
    border-bottom-width : 1px;
    border-left-style: solid;
    border-top-style: solid;
    border-right-color: transparent;
    border-bottom-color: transparent;
}

.profile-navigator > .navigator-option:hover {
    border-bottom-color: rgba(255,68,0,.9);;
    border-right-color: rgba(255,68,0,.9);;
    border-top-color: rgba(255,68,0,.9);
    border-left-color: rgba(255,68,0,.9);;
    border-top-style: solid;
    border-left-style: solid;
}

.profile-navigator > .navigator-option.save-option {
    color: $red;
}

.profile-navigator > .navigator-option.save-option:hover {
    color: $red;
    border-color: $red;
}

.new-mix-button {
    cursor: pointer;
    margin: 10px;
    color: #ff015a;
    font-size: 28px;
}

.new-mix-button:hover {
    color: red;
}

.mix-name-container {
    width: 500px;
    margin-top: 10px;
}

.mix-name {
    font-size: 30px;
    padding: 4px 7px;
    background-color: #3b3358;
    color: white;
    cursor: pointer;
    margin-left: 10px;
}

.back-to-mixes {
    font-size: 20px;
    color: #f92360;
    cursor: pointer;
    margin-left: 50px;
    line-height: 55px;
}

.user-mix:hover {
    background-color: #58e8ba;
}

.user-mix {
    background-color: #7e4ccd;
    color: #d3dcfc;
    font-size: 14pt;
    padding: 15px;
    margin: 10px;
    cursor: pointer;
}

.mix-name-and-back {
    display: flex;
    flex-direction: row;
}

.mix-player-pane {
    float: right !important;
    margin-top: 50px;
    width: 100%;
    min-width: 400px;
    background-color: var(--negative-space);
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
}

.bpm-container {
    margin-left: 10px;
}

.mix-controls-container {
    display: flex;
    padding: 5px;
}

.left-buttons {
    display: flex;
    float: right;
    margin-left: auto;

    .connect-button {
        margin-right: 10px;
        margin-bottom: 5px;
    }
    
    .direct-message-button {
        margin-right: 10px;
        cursor: pointer;
        width: 30px;
        height: 30px;
    }
}

.track-date, .action-date {
    padding: 5px;
    color: white;
}

.action-date {
    margin-left: auto;
}

.spotlight-fader {
    .slider {
        width: 250px;
    }
}

.links-view {
    margin: 10px;
    display: flex;

    .linked-count {
        font-size: 16pt;
        margin: 5px;
        
        .linked-count-number {
            color: #3d30ce;
        }
    }
    
    .linked-member:hover, .guild:hover {
        background-color: blue;
    }

    .guild.selected {
        background-color: #6d6b87;
    }

    .guild-action-buttons {
        display: flex;
        align-items: stretch;
        width: 100%;
    }

    .guild-action-button {
        flex: 1 !important;
        margin: 5px;
    }

    .linked-member, .guild {
        display: flex;
        cursor: pointer;
        border-width: thin;
        border-color: #5a5a5a;
        background-color: #3d30ce;
        color: white;
        margin-left: 5px;
        margin-right: 5px;
        padding: 5px;

       
        .linked-user-name {
            padding: 5px;
        }
        
        .guild-name {
            padding: 5px;
            font-size: 8pt;
            background-color: #303955;
            margin-left: auto;
        }
    }
    
    .linked-members, .guilds {
        width: 300px;
        padding: 5px;
        border-radius: 3px;
        display: flex;
        flex-direction: column;
    }
}

.comments-container {
    margin-top: 10px;
    color: white;
    display: flex;
    flex-direction: column;
    border-top: solid;
    border-width: 1px;
    border-color: #06957a;
    padding-top: 10px;

    .comments-header {
        font-size: 14pt;
    }

    .comment-input-box {
        height: 50px;
    }

    .note-date {
        margin-left: auto;
        font-size: 7pt;
    }

    .sequence-note-container.odd {
        background-color: var(--alternate-color);
    }

    .sequence-note-container {
        margin-top: 5px;
        display: flex;
        padding: 5px;
        font-size: 10pt;
        color: var(--button-color);

        .sequence-note-from {
            cursor: pointer;
        }

        .sequence-note-from:hover {
            color: #c2e7f6;
        }

        .sequence-note-text {
            cursor: pointer;
            margin-left: 10px;
            background-color: white;
            color: var(--color-1);
            padding-left: 5px;
            padding-right: 10px;
            max-width: 600px;
        }
    }

    .hide-notes-button {
        margin-top: 10px;
        background-color: var(--button-color);
        text-align: center;
    }

    .btn-secondary {
        background-color: var(--button-secondary-color) !important;
        border-color: #03614f !important;
    }
}


.green-button {
    background-color: var(--button-color);
}
