.back-to-mixes {
  font-size: 20px;
  color: #f92360;
  cursor: pointer;
  margin-left: 50px;
  line-height: 55px;
}

.user-mix:hover {
  background-color: #58e8ba;
}

.user-mix {
  background-color: #7e4ccd;
  color: #d3dcfc;
  font-size: 14pt;
  padding: 15px;
  margin: 10px;
  cursor: pointer;
}

.mix-name-and-back {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.mix-player-pane {
  float: right !important;
  margin-top: 50px;
  width: 100%;
  min-width: 400px;
  background-color: #0e063b;
  background-color: var(--negative-space);
  margin-bottom: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.bpm-container {
  margin-left: 10px;
}

.mix-controls-container {
  display: -ms-flexbox;
  display: flex;
  padding: 5px;
}

.left-buttons {
  display: -ms-flexbox;
  display: flex;
  float: right;
  margin-left: auto;
}
.left-buttons .connect-button {
  margin-right: 10px;
  margin-bottom: 5px;
}
.left-buttons .direct-message-button {
  margin-right: 10px;
  cursor: pointer;
  width: 30px;
  height: 30px;
}

.track-date, .action-date {
  padding: 5px;
  color: white;
}

.action-date {
  margin-left: auto;
}

.spotlight-fader .slider {
  width: 250px;
}

.links-view {
  margin: 10px;
  display: -ms-flexbox;
  display: flex;
}
.links-view .linked-count {
  font-size: 16pt;
  margin: 5px;
}
.links-view .linked-count .linked-count-number {
  color: #3d30ce;
}
.links-view .linked-member:hover, .links-view .guild:hover {
  background-color: blue;
}
.links-view .guild.selected {
  background-color: #6d6b87;
}
.links-view .guild-action-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  width: 100%;
}
.links-view .guild-action-button {
  -ms-flex: 1 1 !important;
      flex: 1 1 !important;
  margin: 5px;
}
.links-view .linked-member, .links-view .guild {
  display: -ms-flexbox;
  display: flex;
  cursor: pointer;
  border-width: thin;
  border-color: #5a5a5a;
  background-color: #3d30ce;
  color: white;
  margin-left: 5px;
  margin-right: 5px;
  padding: 5px;
}
.links-view .linked-member .linked-user-name, .links-view .guild .linked-user-name {
  padding: 5px;
}
.links-view .linked-member .guild-name, .links-view .guild .guild-name {
  padding: 5px;
  font-size: 8pt;
  background-color: #303955;
  margin-left: auto;
}
.links-view .linked-members, .links-view .guilds {
  width: 300px;
  padding: 5px;
  border-radius: 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.comments-container {
  margin-top: 10px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border-top: solid;
  border-width: 1px;
  border-color: #06957a;
  padding-top: 10px;
}
.comments-container .comments-header {
  font-size: 14pt;
}
.comments-container .comment-input-box {
  height: 50px;
}
.comments-container .note-date {
  margin-left: auto;
  font-size: 7pt;
}
.comments-container .sequence-note-container.odd {
  background-color: #08a184;
  background-color: var(--alternate-color);
}
.comments-container .sequence-note-container {
  margin-top: 5px;
  display: -ms-flexbox;
  display: flex;
  padding: 5px;
  font-size: 10pt;
  color: #03715c;
  color: var(--button-color);
}
.comments-container .sequence-note-container .sequence-note-from {
  cursor: pointer;
}
.comments-container .sequence-note-container .sequence-note-from:hover {
  color: #c2e7f6;
}
.comments-container .sequence-note-container .sequence-note-text {
  cursor: pointer;
  margin-left: 10px;
  background-color: white;
  color: #008d70;
  color: var(--color-1);
  padding-left: 5px;
  padding-right: 10px;
  max-width: 600px;
}
.comments-container .hide-notes-button {
  margin-top: 10px;
  background-color: #03715c;
  background-color: var(--button-color);
  text-align: center;
}
.comments-container .btn-secondary {
  background-color: #0c4b3f !important;
  background-color: var(--button-secondary-color) !important;
  border-color: #03614f !important;
}

.green-button {
  background-color: #03715c;
  background-color: var(--button-color);
}
